
import VHeader from '@/components/v-header/v-header'
import Scroller from 'vux/src/components/scroller/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { localUser } from '@/assets/js/local'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
data () {
return {
VRStatus: true,
styles: {
heght: '0px'
},
user: {},
show: false,
version: 0
}
},
mounted () {
/* eslint-disable */

/* eslint-disable */
},
created () {
this.user = localUser.get()
this.styles.heght = window.innerHeight - 64 + 'px'

nativeBridge.getVRStatus((arg) => {
this.VRStatus = arg
})
this.version = window.android.getVersionCode()
},
methods: {
handleAlert () {
this.show = true
},
hangdleClickLogout () {
const JIM = this.$store.getters.JIM
if (JIM) {
JIM.loginOut()
}
this.$store.commit('SET_LOGIN_ANMATION', 'myRorate')
localUser.remove(this.user)
this.$router.push('/user')
},
handleSetVr (value) {
nativeBridge.toggleVR(this, value)
},


onCancel () {
},
onConfirm () {
this.hangdleClickLogout()
},
updateVersion () {
window.android.updateVersion()
},
handlemessage () {
this.$router.push('/user/personmessage')
}
},
components: {
VHeader,
Scroller,
XSwitch,
XButton,
Cell,
Confirm,
Group
}
}



